html {
  scroll-behavior: smooth;
}

/* WebKit Browsers */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.3);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 255, 255, 0.5);
}

/* Firefox */
body {
  scrollbar-width: thin;
  scrollbar-color: rgba(255, 255, 255, 0.3) transparent;
}

/* General styles for the section */
#about {
  text-align: center;
  padding: 2em 1em;
  background: #12121277;
  color: #e0e0e0;
  position: relative;
}

/* Section title and underline */
.section-title {
  font-size: 2.5em;
  margin-bottom: 0.5em;
  position: relative;
  display: inline-block;
}

.section-title::after {
  content: '';
  display: block;
  width: 50%;
  margin: 0.5em auto;
  height: 2px;
  background: #17e90c;
}

/* Profile picture styling */
.profile-pic {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  overflow: hidden;
  margin: 1em auto; /* Center the picture */
  transition: transform 0.3s ease;
}

.profile-pic img {
  width: 100%;
  height: auto;
  display: block;
  border-radius: 50%;
}

.profile-pic:hover {
  transform: scale(1.1); /* Slightly enlarge the picture on hover */
}

/* Info text */
.intro-info {
  margin: 2em auto 0;
  max-width: 800px;
  text-align: center;
  padding: 0 1em; /* Add padding for better readability */
  animation: fadeIn 2s; /* Add a fade-in animation */
}

.intro-info p {
  margin: 1em 0;
  line-height: 1.6;
}

/* Fade-in animation */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .section-title {
    font-size: 2em;
  }

  .profile-pic {
    width: 100px;
    height: 100px;
  }

  .intro-info p {
    font-size: 0.9em;
  }
}



